import React, { useState, useEffect } from "react";
import axios from "axios";
import UserCard from "./UserCard";
import Pagination from "react-js-pagination";

//fetch random user data with an API
const fetchRandomData = () => {
  return axios
    .get(`https://randomuser.me/api/?results=100`)
    .then(({ data }) => {
      console.log("userInfoData", data);
      return data;
    })
    .catch((error) => {
      console.log(error);
    });
};

const UserList = () => {
  const [userInfos, setUserInfos] = useState([]);
  const [searchValue, setSearchValue] = useState("");
  const [localVar, setlocalVar] = useState([]);

  const usersPerPage = 9;
  const [activePage, setCurrentPage] = useState(1);

  const indexOfLastUser = activePage * usersPerPage;
  const indexOfFirstTodo = indexOfLastUser - usersPerPage;
  const currentUsers = userInfos.slice(indexOfFirstTodo, indexOfLastUser);

  const handlePageChange = (pageNumber) => {
    console.log(`active page is ${pageNumber}`);
    setCurrentPage(pageNumber);
  };

  useEffect(() => {
    fetchRandomData().then((data) => {
      if (data === undefined) return;
      setUserInfos(data.results);
      localStorage.setItem("userInfo", JSON.stringify(data.results));
    });
  }, []);

  useEffect(() => {
    let filterData = userInfos.filter(
      (data) => !data.name.first.toLowerCase().search(searchValue)
    );
    setlocalVar(filterData);
    localStorage.setItem("searchValue", JSON.stringify(filterData));
    console.log("filterdata", filterData);
  }, [searchValue]);
  return (
    <>
      {/* header */}
      <div className="nav">
        <a href="./" className="brand">
          RandomUser_Details
        </a>
        <form>
          <input
            type="text"
            value={searchValue}
            className="search"
            placeholder="Search Random User"
            onChange={(e) => setSearchValue(e.target.value)}
          />
        </form>
      </div>

      <div className="header-heading">
        <h1>
          <i>User Details Zone</i>
        </h1>
      </div>
      {/* main body with logic */}
      <div className="container_">
        {searchValue.length > 0
          ? localVar.map((data, idx) => {
              return <UserCard {...data} key={idx} />;
            })
          : currentUsers.map((userInfo, idx) => {
              // console.log(userInfo);
              return <UserCard {...userInfo} key={idx} />;
            })}
      </div>

      <div className="pagination">
        <Pagination
          prevPageText="prev"
          nextPageText="next"
          firstPageText="first"
          lastPageText="last"
          activePage={activePage}
          itemsCountPerPage={10}
          pageRangeDisplayed={5}
          totalItemsCount={userInfos.length}
          onChange={handlePageChange}
        />
      </div>
      {/* footer */}
      <footer className="footer">
        <p>
          Created by &nbsp;
          <b>
            <a href="_target" target="_">
              aravindhalahalli
            </a>
          </b>
          &nbsp; powered by &nbsp;
          <a href="https://randomuser.me/" target="_">
            <img
              className="randomuser"
              src="data:image/png;base64,"
              alt="randomuser"
            />
          </a>
        </p>{" "}
      </footer>
    </>
  );
};

export default UserList;
